<template>
  <div class="main dataSummary quality-info">
    <div class="main-search">
      <div class="search-item">
        <span class="item-left">外呼团队</span>
        <span class="item-right">
          <el-select v-model="search.corpName" multiple collapse-tags @change="changeStaff('corp')">
            <template v-for="item in teamData">
              <el-option :label="item.memo" :value="item.memo"  :key="item.id">{{item.memo}}</el-option>
            </template>
          </el-select>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">品牌</span>
        <span class="item-right">
          <el-select v-model="search.projName" multiple collapse-tags @change="changeStaff('proj')">
            <template v-for="item in projData">
              <el-option :label="item.memo" :value="item.memo"  :key="item.id">{{item.memo}}</el-option>
            </template>
          </el-select>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">坐席工号</span>
        <span class="item-right">
          <el-input placeholder="输入坐席工号" v-model="search.uid"></el-input>
        </span>
      </div>
      <button type="button" class="btn-search" @click="search_detail">查询</button>
    </div>
    <div class="main-table">
      <div class="table-msg">
        <div class="msg-left">
          <p>查询符合上述条件的数据<span>{{list.totalCount}}</span>条</p>
        </div>
        <div class="msg-right">
          <el-button type="button" class="btn-small" @click="employeeDownload">导出模板</el-button>
        </div>
        <div class="msg-right">
          <el-upload
            class="upload-demo"
            ref="upload"
            :action="uploadUrl()"
            multiple
            :show-file-list="false"
            :file-list="fileList"
            :auto-upload="true"
            :before-upload="beforeAvatarUpload"
            :on-success="uploadSuccess">
            <!--//:on-error="uploadFalse" // :on-success="uploadSuccess"-->
            <el-button type="button" class="btn-small">导入文件</el-button>
          </el-upload>&nbsp;
        </div>
      </div>
      <el-table
        :data="list.data"
        style="width: 100%"
        border
        :default-sort = "{prop: 'date', order: 'descending'}">
        <el-table-column
          prop="corp"
          align="center"
          label="外呼团队">
        </el-table-column>
        <el-table-column
          prop="proj"
          align="center"
          label="品牌">
        </el-table-column>
        <el-table-column
          prop="uid"
          align="center"
          label="坐席工号">
        </el-table-column>
        <el-table-column
          prop="uname"
          align="center"
          label="坐席姓名">
        </el-table-column>
        <el-table-column
          prop="passdate"
          align="center"
          label="电测通过日期"
          width="160px">
        </el-table-column>
        <el-table-column
          prop="combatdate"
          align="center"
          label="实战时间"
          width="160px">
        </el-table-column>
        <el-table-column
          prop="offline"
          align="center"
          label="下线时间"
          width="160px">
        </el-table-column>
        <el-table-column
          prop="backonline"
          align="center"
          label="重新上线时间"
          width="170px">
        </el-table-column>
        <el-table-column
          prop="offtime"
          align="center"
          label="下线次数">
        </el-table-column>
        <el-table-column
          align="center"
          label="操作"
          width="80px">
          <template slot-scope="scope">
            <a href="javascript:void(0)" @click="edit(scope.$index,scope.row)">修改</a>
          </template>
        </el-table-column>
      </el-table>
      <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
    <el-dialog class="dialog-box"
      :visible.sync="dialog_edit"
      width="35%">
      <div class="main-edit" style="max-height: 300px; overflow-y: auto">
        <div class="edit-box">
          <div class="info-item">
            <span class="item-left">外呼团队</span>
            <span class="item-right">
              <el-input v-model="editContent.corpName" readonly style="width: 60%;"></el-input>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">品牌</span>
            <span class="item-right">
              <el-input v-model="editContent.projName" readonly style="width: 60%;"></el-input>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">坐席工号</span>
            <span class="item-right">
              <el-input v-model="editContent.uid" readonly style="width: 60%;"></el-input>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">坐席姓名</span>
            <span class="item-right">
              <el-input v-model="editContent.uname" readonly style="width: 60%;"></el-input>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">电测通过日期</span>
            <span class="item-right">
              <el-date-picker
                v-model="editContent.through"
                type="datetime"
                placeholder="选择通过日期"
                style="width: 60%">
              </el-date-picker>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">实战时间</span>
            <span class="item-right">
              <el-date-picker
                v-model="editContent.combat"
                type="datetime"
                placeholder="选择通过日期"
                style="width: 60%">
              </el-date-picker>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">下线时间</span>
            <span class="item-right">
              <el-date-picker
                v-model="editContent.offline"
                type="datetime"
                placeholder="选择通过日期"
                style="width: 60%">
              </el-date-picker>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">重新上线时间</span>
            <span class="item-right">
              <el-date-picker
                v-model="editContent.online"
                type="datetime"
                placeholder="选择通过日期"
                style="width: 60%">
              </el-date-picker>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">下线次数</span>
            <span class="item-right">
              <el-input v-model="editContent.offlinetime" style="width: 60%;"></el-input>
            </span>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save">保存</el-button>
        <el-button @click="dialog_edit = false">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {format} from '@/config/util'
  import {employeeFile, employee, update_workplace} from '@/api/index'
  import pageTable from '@/components/pager'
  import ajax from '@/config/ajax'
  import {put, del} from '@/config/oss'
  import Qs from 'qs'
  import moment from 'moment'
  export default {
    name: "employee",
    components:{pageTable},
    data() {
      return {
        search: {
          corp: [], // 外呼团队
          corpName: [],  //外呼团队名臣
          prjid_local: [], // 品牌
          projName: [], //品牌名称
          uid: '',  // 坐席工号
          size: 20, // 每页数量
          page: 1 // 页码
        },
        list: {
          data: [], // 数据集
          totalPage: 0, // 总页数
          totalCount: 0 // 总条数
        },
        editContent: {
          id: '',
          corpName: '', //外呼团队
          projName: '', //品牌
          uid: '', //坐席工号
          uname: '', //坐席姓名
          through: '', //电测通过日期
          combat: '', //实战时间
          offline: '', //下线时间
          online: '', //重新上线时间
          offlinetime: '' //下线次数
        },
        dialog_upload: false,  //上传文件弹框
        isCorpContainAll: true,     //外呼团队是否选则了全选
        isProjContainAll: true,     //品牌是否选则了全选
        isUidAll: true,
        teamData: [],  //外呼团队字典数组
        projData: [],  //品牌字典数组
        worknum: [], //坐席工号字典数组
        fileList: [],   //文件数组
        dialog_edit: false, //修改弹出框
        editData: ''
      }
    },
    created(){
      this.teamData = JSON.parse(localStorage.getItem("cropid"));
      this.projData = JSON.parse(localStorage.getItem("projid"));
      this.search.corp[0] = this.teamData[0].id;
      this.search.prjid_local[0] = this.projData[0].id;
      this.search.corpName[0] = this.teamData[0].memo;
      this.search.projName[0] = this.projData[0].memo;
      this.getInitialList();
    },
    methods:{
      employeeDownload() {
        return ajax.request({
          method: 'post',
          url: 'employee/download',
          responseType: 'arraybuffer'
        }).then((res) => {
          let fileDownload = require("js-file-download");
          fileDownload(res,"职场展示-职场人员"+format(new Date(), 'yyyy-MM-dd hh-mm-ss')+".xlsx");
        });
      },
      save() {
        var params = {
          id: this.editContent.id,
          passdate: moment(this.editContent.through).format('YYYY/MM/DD HH:mm:ss'),
          combatdate: moment(this.editContent.combat).format('YYYY/MM/DD HH:mm:ss'),
          offline: moment(this.editContent.offline).format('YYYY/MM/DD HH:mm:ss'),
          backonline: moment(this.editContent.online).format('YYYY/MM/DD HH:mm:ss'),
          offtime: this.editContent.offlinetime
        }
        update_workplace(params).then((res) => {
          this.getInitialList();
        }).catch((error) => {
          console.log(error)
        })
        this.dialog_edit = false;
      },
      edit(index, list) {
        this.dialog_edit = true;
        this.editContent.id = list.id;
        // console.log(this.editContent.id)
        this.editContent.corpName = list.corp;
        this.editContent.projName = list.proj;
        this.editContent.uid = list.uid;
        this.editContent.uname = list.uname;
        this.editContent.through = list.passdate;
        this.editContent.combat = list.combatdate;
        this.editContent.offline = list.offline;
        this.editContent.online = list.backonline;
        this.editContent.offlinetime = list.offtime;
        this.editData = list;
      },
      getInitialList(){
        var params = {
          corp: this.search.corp.join(","),
          proj: this.search.prjid_local.join(","),
          uid: this.search.uid,
          page:this.search.page,
          size:this.search.size
        }
        // console.log(params)
        employee(this.search.corp.join(","), this.search.prjid_local.join(","), this.search.uid, this.search.page, this.search.size).then((res)=>{
          this.list.data=res.data.list;
          // console.log(res.data.list)
          this.list.totalCount=res.data.totalCount;
          this.list.totalPage=res.data.totalPage;
        }).catch((error)=>{
          console.log(error)
        })
      },
      //品牌及外呼团队多选
      changeStaff(type){
        if(type == 'corp'){
          this.search.corp = this.search.corpName;
          if(this.isCorpContainAll){
            this.isCorpContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
            this.search.corp.splice(0, 1);// 则删除第一个全部
          }else{
            this.isCorpContainAll = this.search.corp.some(value => value === '全部')
            if(this.isCorpContainAll){
              this.search.corp = [0];
              this.search.corpName = ['全部'];
            }else{
              if(this.search.corp.length == this.teamData.length-1){
                this.search.corp = [0];
                this.search.corpName = ['全部']
                this.isCorpContainAll = true;
              }
            }
          }
        }else if(type == 'proj'){
          this.search.prjid_local = this.search.projName;
          if(this.isProjContainAll){
            this.isProjContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
            this.search.prjid_local.splice(0, 1);// 则删除第一个全部
          }else{
            this.isProjContainAll = this.search.prjid_local.some(value => value === '全部')
            if(this.isProjContainAll){
              this.search.prjid_local = [0];
              this.search.projName = ['全部'];
            }else{
              if(this.search.prjid_local.length == this.projData.length-1){
                this.search.prjid_local = [0];
                this.search.projName = ['全部'];
                this.isProjContainAll = true;
              }
            }
          }
        }
      },
      // 查询
      search_detail () {
        if(this.search.corp.length == 0){
          this.$message({
            message: '请选择外呼团队',
            type: 'warning'
          });
          return
        }else if(this.search.prjid_local.length == 0){
          this.$message({
            message: '请选择品牌',
            type: 'warning'
          });
          return
        }
        this.search.page = 1;
        this.getInitialList()
      },
      upload(){
        this.dialog_upload = true;
      },
      uploadUrl(){
        return "http://39.100.61.222/employee/upload?file=";
      },
      uploadSuccess(response, file, fileList) {
        if (response.code == '200') {
          this.$message.success("导入成功");
        } else {
          this.$message.success("导入失败")
        }
      },
      //上传文件之前调用的函数
      beforeAvatarUpload(file) {
        const extension = file.name.split(".")[1] === "xls";
        const extension2 = file.name.split(".")[1] === "xlsx";
        const isLt2M = file.size / 1024 / 1024 < 10;
        if (!extension && !extension2) {
          alert("上传模板只能是 xls、xlsx、doc、docx 格式!");
        }
        // if (!isLt2M) {
        //   console.log("上传模板大小不能超过 10MB!");
        // }
        // console.log(file)
        return extension || extension2;
      },
      //分页
      change_data(index,size){
        this.search.page=index;
        this.search.size=size;
        this.getInitialList();
      }
    }
  }
</script>

<style scoped>
  @import "../../assets/css/pages/main.css";
  .quality-info{
    padding-left: 15px;
  }
  select.txt-common{
    height:40px;
  }
  .el-select-dropdown__wrap ul li{
    padding-left: 20px !important;
    display: block;
  }

</style>



