import OSS from 'ali-oss'

const Client=new OSS({
  accessKeyId:'LTAIUEAyF5F1rtjD',
  accessKeySecret:'6E4HR2AI8Bwd9DlfcjcwjksjFfpkzD',
  region: 'oss-cn-zhangjiakou', // oss地区
  bucket: 'dmp-mm',  //oss上传文件名称(已创建)
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
  }
});
export function put(date, timestamp, file) {
  return Client.put(date+"/"+timestamp+"/"+file.name,file.raw);
}
export function del(file) {
  return Client.delete(file.name,file.raw);
}



